import React, { useRef, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { updateCodeWorkingText, updateSelectionBorder } from '../store/triangleSlice';
import Editor from "@monaco-editor/react";
import Template from './Template';

export default function CodeEditor() {
    const workingText = useSelector((state) => state.triangle.codeWorkingText);
    const ref = useRef(null);
    const dispatch = useDispatch();
    ref.itemChanged = useSelector((state) => state.triangle.itemChanged);
    ref.item = useSelector((state) => state.triangle.item);
    ref.breakpoint = useSelector((state) => state.triangle.breakpoint);
    ref.codeEditorActive = useSelector((state) => state.triangle.codeEditor);
    let template = new Template();

    useEffect(() => {
        dispatch(updateCodeWorkingText(-1));
    }, [ref.itemChanged, ref.item, dispatch]);

    if (!ref.item) {
        ref.value = "";
    } else if (workingText === -1) {
        let rule = template.selector(ref.breakpoint, "#" + ref.item);
        ref.value = "@media " + rule.parentRule.conditionText + " {\n\t"
        + formatCSS(rule.cssText).trim() + "\n}";
    } else {
        ref.value = workingText;
    }

    function formatCSS(csstext) {
        return csstext.replace(
            /([{;]\s*)/g,
            (match, $1, offset, original) => {
                return ($1).trim() + "\n\t\t";
            }
        ).replace("\t}", "}");
    }

    function handleEditorDidMount(editor, monaco) {
        ref.editor = editor;
    }

    function handleEditorChange(newValue) {
        if (!ref.item) {
            return;
        }
        let regex = RegExp(/{([^{}]+)}/g)
        let isolateStyles = regex.exec(newValue);
        if (isolateStyles === null) {
            return;
        }
        if (isolateStyles[1] === null) {
            return;
        }
        let newCSS = isolateStyles[1];
        template.selector(ref.breakpoint, "#" + ref.item).style.cssText = newCSS;
        dispatch(updateSelectionBorder());
        dispatch(updateCodeWorkingText(newValue));
    }

    return <>
        <Editor
            height="calc(100% - 30px)"
            width="100%"
            theme="vs-dark"
            defaultLanguage="css"
            value={ref.value}
            onChange={handleEditorChange}
            options={{
                fontSize: "16px",
                renderValidationDecorations: "off"
            }}
            onMount={handleEditorDidMount} />
    </>
}