import React from 'react';
import { useSelector } from 'react-redux';
import Template from './Template';
import './SelectionBorder.css';

function SelectionBorder() {

    const item = useSelector((state) => state.triangle.item);

    // re-render when the state changes
    useSelector((state) => state.triangle.itemChanged);
    useSelector((state) => state.triangle.updateSelectionBorder);

    if (item) {
        let template = new Template();
        let itemNode = template.document.getElementById(item);
        let rect = itemNode.getBoundingClientRect();

        return <>
            <div id="selectionBorder" style={{
                width: rect.width + 4,
                height: rect.height + 4,
                top: rect.top - 3 + template.iframe.getBoundingClientRect().top,
                left: rect.left - 3 + template.iframe.getBoundingClientRect().left
            }}><div id="noClickThru" /></div>
        </>;
    }
}

export default SelectionBorder;