import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { itemChanged } from '../../store/triangleSlice';
import Template from '../Template';
import ButtonUnstyled from '@mui/base/ButtonUnstyled';

import '../OptionsBar.css';
import './OptionsColors.css';

function OptionsColors() {
    const dispatch = useDispatch();
    const item = useSelector((state) => state.triangle.item);
    const breakpoint = useSelector((state) => state.triangle.breakpoint);

    function changeColor(e) {
        if (!item) return;
        let color = e.target.style.backgroundColor;
        let template = new Template();
        template.selector(breakpoint, "#" + item).style.backgroundColor = color;
        dispatch(itemChanged());
    }

    const colors = [
        "#F79489", "#F8AFA6", "#FADCD9", "#F9F1F0",
        "#05445E", "#189AB4", "#75E6DA", "#D4F1F4",
        "#FF8300", "#F99B45", "#FBC490", "#F9EAC2",
    ];
    const listButtons = colors.map((color) => 
        <ButtonUnstyled className="Swatch" 
        style={{backgroundColor:color}}
        onClick={changeColor}
        key={color.toString()} />
    );

    return <>
        {listButtons}
    </>;

}

export default OptionsColors;