import useKeyDown from '../hooks/useKeyDown';
import { useSelector, useDispatch } from 'react-redux';
import { selectItem, toggleCodeEditor } from '../store/triangleSlice'
import Template from './Template';

export default function KeyBinds() {
    const dispatch = useDispatch();
    let template = new Template();

    function deselectItem() {
        dispatch(selectItem(false));
    }
    useKeyDown(['Escape'], deselectItem);
    useKeyDown(['Escape'], deselectItem, template.document);

    const item = useSelector((state) => state.triangle.item);
    function deleteItem() {
        // implement a way to remove styles of deleted elements from CSS sheets
        template.document.getElementById(item).remove();
        dispatch(selectItem(false));
    }
    useKeyDown(['Delete', 'Backspace'], deleteItem);
    useKeyDown(['Delete', 'Backspace'], deleteItem, template.document);

    // if you edit this, then edit OptionsLayout component too
    function newElement() {
        let newItem = template.newElement();
        newItem.addEventListener("mousedown", () => {
            dispatch(selectItem(newItem.id))
        }, true);
        dispatch(selectItem(newItem.id));
    }
    useKeyDown(['n'], newElement);
    useKeyDown(['n'], newElement, template.document);

    function toggleMonaco() {
        dispatch(toggleCodeEditor());
    }
    useKeyDown(['c'], toggleMonaco);
    useKeyDown(['c'], toggleMonaco, template.document);
}