import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { toggleCodeEditor, updateCodeResizable } from '../store/triangleSlice';
import { Resizable } from 'react-resizable';
import CloseIcon from '@mui/icons-material/Close';
import CodeEditor from './CodeEditor';
import 'react-resizable/css/styles.css';
import './CodeDraggable.css';

export default function CodeResizable(props) {
    const coords = useSelector((state) => state.triangle.codeResizable);
    const dispatch = useDispatch();

    function onResize(e, {element, size, handle}) {
        dispatch(updateCodeResizable(size));
    }

    return <>
        <Resizable 
            height={coords.height}
            width={coords.width}
            onResize={onResize}
            className="CodeResizable"
            minConstraints={[100, 100]}
            resizeHandles={["se", "e", "s"]}
            onResizeStart={() => { props.toggleIframeEvents("none") }}
            onResizeStop={() => { props.toggleIframeEvents("auto") }}>
            <div style={{width: coords.width + 'px', height: coords.height + 'px'}}>
                <div className="CodeHandle">
                    CSS
                    <CloseIcon
                        onClick={() => {
                            dispatch(toggleCodeEditor())
                        }}
                        fontSize="small"
                        sx={{ cursor: "pointer" }}
                    />
                </div>

                <CodeEditor />
            </div>
        </Resizable>
`    </>
}