import { breakpointMap } from './breakpointMap';

class Template {
    iframe = document.getElementById("template");
    document = this.iframe.contentDocument;
    mainsheet = this.document.getElementById("styles").sheet;

    breakpoint(size) {
        return this.mainsheet.cssRules[
            breakpointMap[size].styleSheetIndex
        ];
    }

    selector(size, selector) {
        let selectorIndex = -1;
        let rules = this.breakpoint(size).cssRules;
        for (var i = 0; i < rules.length; i++) {
            if (rules[i].selectorText === selector) selectorIndex = i;
        }
        return rules[selectorIndex];
    }

    newElement() {
        let newDiv = document.createElement("div");
        let random = "item" + Math.floor(Math.random() * 1000000);
        newDiv.id = random;
        newDiv.setAttribute("triangle-class", "templateItem");
        let newRule = `#${newDiv.id} {
            display:flex;
            background-color:#f5f2f0;
            height:auto;
            width:100%;
            padding:15px;
        }`;

        this.document.body.appendChild(newDiv);

        this.breakpoint("xxl").insertRule(newRule);
        this.breakpoint("xl").insertRule("#" + newDiv.id + "{}");
        this.breakpoint("lg").insertRule("#" + newDiv.id + "{}");
        this.breakpoint("md").insertRule("#" + newDiv.id + "{}");
        this.breakpoint("sm").insertRule("#" + newDiv.id + "{}");
        this.breakpoint("xs").insertRule("#" + newDiv.id + "{}");

        this.iframe.contentWindow.scrollTo(0, newDiv.offsetTop);
        return newDiv;
    }

    rect(item) {
        return Object.values(item.getBoundingClientRect().toJSON()).join("");
    }
}

export default Template;