import { useSelector } from 'react-redux';
import Template from './Template';

export default function MirrorMode() {
    const mirrorModeActive = useSelector((state) => state.triangle.mirrorMode);
    useSelector((state) => state.triangle.itemChanged);
    // next line is to listen for when the code editor makes a change
    useSelector((state) => state.triangle.updateSelectionBorder);

    let template = new Template();

    if (mirrorModeActive) {
        console.log("active");
        const targetNode = template.document.body;
        const observer = new MutationObserver(updateMirror);
        observer.observe(targetNode, {
            childList: true,
            attributes: true,
            subtree: true
        });
        updateMirror();
    }
}

export function updateMirror() {
    let template = new Template();
    let previewWindow = window.open("", "triangle");
    previewWindow.document.title = "Triangle | Preview";
    let newSheet;

    previewWindow.document.body.innerHTML = template.document.body.innerHTML;

    if (!previewWindow.document.getElementById("styles")) {
        let link = document.createElement("link");
        link.rel = 'stylesheet';
        link.type = 'text/css';
        link.href = 'http://trianglecms.com/react/export-default.css'; // name of your css file
        // link.href += "?a=" + Math.random(); // for testing only. circumvents caching.
        previewWindow.document.head.appendChild(link);

        let style = document.createElement("style");
        style.id = "styles";
        previewWindow.document.head.appendChild(style);
        newSheet = previewWindow.document.getElementById("styles").sheet;
    } else {
        newSheet = previewWindow.document.getElementById("styles").sheet;
        for (let i = 0; i < newSheet.cssRules.length; i++) {
            newSheet.removeRule(i)
        }
    }

    for (let i = template.mainsheet.cssRules.length - 1; i >= 0; i--) {
        newSheet.insertRule(template.mainsheet.cssRules[i].cssText);
    }
}