import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import Draggable from 'react-draggable';
import Template from './Template';
import CodeResizable from './CodeResizable';
import './CodeDraggable.css';

export default function CodeDraggable() {
    const [draggablePosition, setDraggablePosition] = useState({x: 0, y: 71});

    // useEffect(()=>{
    //     console.log("mount");
    //     return () => {
    //         console.log("draggable", "unmount")
    //     }
    // },[]);

    const template = new Template();
    function toggleIframeEvents(value) {
        template.iframe.style.pointerEvents = value;
    }

    function handleDragStart() {
        toggleIframeEvents("none");
    }

    function handleDragStop() {
        toggleIframeEvents("auto");
    }

    function handleDrag(e, data) {
        setDraggablePosition({x: data.x, y: data.y});
    }

    const codeEditorActive = useSelector((state) => state.triangle.codeEditor);
    if (codeEditorActive) {
        return <>
            <Draggable
                handle=".CodeHandle"
                defaultPosition={{ x: 0, y: 71 }}
                position={{x: draggablePosition.x, y: draggablePosition.y}}
                scale={1}
                bounds="body"
                onStart={handleDragStart}
                onDrag = {handleDrag}
                onStop={handleDragStop}>
                    <div className="CodeDraggable">
                        <CodeResizable toggleIframeEvents={toggleIframeEvents} />
                    </div>
            </Draggable>
        </>;
    }
}