import React from 'react';
import { useSelector } from 'react-redux';

import Dashboard from './components/Dashboard';
import Triangle from './components/Triangle';

// class App extends React.Component {
function App() {
    const mode = useSelector((state) => state.triangle.mode);
    let modeResult;
    if (mode === "dashboard") {
        modeResult = <Dashboard />
    } else if (mode === "editor") {
        modeResult = <Triangle />
    }
    return modeResult;
}

export default App;