import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  mode: "dashboard",
  item: false,
  itemChanged: false,
  updateSelectionBorder: false,
  breakpoint: "xxl",
  changesSaved: false,
  codeEditor: false,
  codeResizable: {
    width: 620,
    height: 300
  },
  codeWorkingText: -1,
  mirrorMode: false,
};

export const triangleSlice = createSlice({
  name: 'triangle',
  initialState,

  reducers: {
    switchMode: (state, action) => {
      state.mode = action.payload;
      state.breakpoint = initialState.breakpoint;
      state.codeEditor = initialState.codeEditor;
      state.item = initialState.item;
    },
    selectItem: (state, action) => {
      state.item = action.payload;
    },
    itemChanged: (state) => {
      state.itemChanged = !state.itemChanged;
    },
    updateSelectionBorder: (state) => {
      state.updateSelectionBorder = !state.updateSelectionBorder;
    },
    switchBreakpoint: (state, action) => {
      state.breakpoint = action.payload;
    },
    changesSaved: (state, action) => {
      state.changesSaved = action.payload;
    },
    toggleCodeEditor: (state) => {
      state.codeEditor = !state.codeEditor;
    },
    updateCodeResizable: (state, action) => {
      state.codeResizable = action.payload;
    },
    updateCodeWorkingText: (state, action) => {
      state.codeWorkingText = action.payload;
    },
    toggleMirrorMode: (state, action) => {
      state.mirrorMode = action.payload;
    },
  },
});

export const {
  switchMode,
  selectItem,
  itemChanged,
  updateSelectionBorder,
  switchBreakpoint,
  changesSaved,
  toggleCodeEditor,
  updateCodeResizable,
  updateCodeWorkingText,
  toggleMirrorMode,
} = triangleSlice.actions;

export default triangleSlice.reducer;
