import { useCallback, useEffect, useLayoutEffect, useRef } from 'react';

const useKeyDown = (keys, callback, node = null) => {

  const callbackRef = useRef(callback);
  useLayoutEffect(() => {
    callbackRef.current = callback;
  });

  const handleKeyDown = useCallback(
    (event) => {
      let tags = ["TEXTAREA", "INPUT"];
      if (tags.some((tag) => event.target.tagName === tag)) {
        return;
      }
      if (keys.some((key) => event.key === key)) {
        callbackRef.current(event);
      }
    },
    [keys]
  );

  useEffect(() => {
    // target is either the provided node or the document
    const targetNode = node ?? document;
    targetNode &&
      targetNode.addEventListener("keydown", handleKeyDown);

    return () =>
      targetNode &&
        targetNode.removeEventListener("keydown", handleKeyDown);
  }, [handleKeyDown, node]);
};

export default useKeyDown;
