import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { switchBreakpoint } from '../../store/triangleSlice'
import ListItemText from '@mui/material/ListItemText';
import ListItemIcon from '@mui/material/ListItemIcon';
import Typography from '@mui/material/Typography';
import StarBorderIcon from '@mui/icons-material/StarBorder';
import TvIcon from '@mui/icons-material/Tv';
import LaptopIcon from '@mui/icons-material/Laptop';
import TabletMacIcon from '@mui/icons-material/TabletMac';
import StayCurrentLandscapeIcon from '@mui/icons-material/StayCurrentLandscape';
import SmartphoneIcon from '@mui/icons-material/Smartphone';

import ButtonUnstyled from '@mui/base/ButtonUnstyled';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import PhonelinkIcon from '@mui/icons-material/Phonelink';

import '../OptionsBar.css';

export default function OptionsBreakpoints() {
    const dispatch = useDispatch();
    let item = useSelector((state) => state.triangle.item);
    let currentBreakpoint = useSelector((state) => state.triangle.breakpoint);

    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = (breakpoint) => {
        dispatch(switchBreakpoint(breakpoint));
        setAnchorEl(null);
    };

    return (
        <>
            <ButtonUnstyled className="OptionsBtn"
                aria-controls={open ? 'basic-menu' : undefined}
                aria-haspopup="true"
                aria-expanded={open ? 'true' : undefined}
                onClick={handleClick}>
                <PhonelinkIcon sx={{ fontSize: 20 }} />
            </ButtonUnstyled>
            <Menu
                anchorEl={anchorEl}
                open={open}
                onClose={() => { handleClose(currentBreakpoint) }}
                MenuListProps={{
                    'aria-labelledby': 'basic-button',
                }}>

                <MenuItem onClick={() => { handleClose("xxl") }}>
                    <ListItemIcon>
                        <StarBorderIcon fontSize={"small"} />
                    </ListItemIcon>
                    <ListItemText sx={{marginRight:2}}>All</ListItemText>
                    <Typography variant="body2" color="text.secondary">
                        ( &gt; 1400px)
                    </Typography>
                </MenuItem>

                <MenuItem onClick={() => { handleClose("xl") }}>
                    <ListItemIcon>
                        <TvIcon fontSize={"small"} />
                    </ListItemIcon>
                    <ListItemText sx={{marginRight:2}}>XL</ListItemText>
                    <Typography variant="body2" color="text.secondary">
                        (1200px)
                    </Typography>
                </MenuItem>

                <MenuItem onClick={() => { handleClose("lg") }}>
                    <ListItemIcon>
                        <LaptopIcon fontSize={"small"} />
                    </ListItemIcon>
                    <ListItemText sx={{marginRight:2}}>Large</ListItemText>
                    <Typography variant="body2" color="text.secondary">
                        (992px)
                    </Typography>
                </MenuItem>

                <MenuItem onClick={() => { handleClose("md") }}>
                    <ListItemIcon>
                        <TabletMacIcon fontSize={"small"} />
                    </ListItemIcon>
                    <ListItemText sx={{marginRight:2}}>Medium</ListItemText>
                    <Typography variant="body2" color="text.secondary">
                        (768px)
                    </Typography>
                </MenuItem>

                <MenuItem onClick={() => { handleClose("sm") }}>
                    <ListItemIcon>
                        <StayCurrentLandscapeIcon fontSize={"small"} />
                    </ListItemIcon>
                    <ListItemText sx={{marginRight:2}}>Small</ListItemText>
                    <Typography variant="body2" color="text.secondary">
                        (576px)
                    </Typography>
                </MenuItem>

                <MenuItem onClick={() => { handleClose("xs") }}>
                    <ListItemIcon>
                        <SmartphoneIcon fontSize={"small"} />
                    </ListItemIcon>
                    <ListItemText sx={{marginRight:2}}>XS</ListItemText>
                    <Typography variant="body2" color="text.secondary">
                        (&lt; 576px)
                    </Typography>
                </MenuItem>
            </Menu>
        </>
    );
}
