import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { updateSelectionBorder } from '../store/triangleSlice'
import { breakpointMap } from './breakpointMap';
import OptionsBar from './OptionsBar';
import KeyBinds from './KeyBinds';
import CodeDraggable from './CodeDraggable';
import MirrorMode from './MirrorMode';
import SelectionBorder from './SelectionBorder';
import './Triangle.css';

function Triangle() {
    const dispatch = useDispatch();

    const [iframeLoaded, setIframeLoaded] = useState(false);

    useEffect(()=>{
        if (iframeLoaded) {
            let template = document.getElementById("template")
            const resizeObserver = new ResizeObserver(() => {
                dispatch(updateSelectionBorder());
            });
            resizeObserver.observe(template);
            
            return ()=>{
                resizeObserver.disconnect();
            }
        }
    },[iframeLoaded, dispatch]);

    function onIframeLoad() {
        let template = document.getElementById("template");

        let link = document.createElement("link");
        link.rel = 'stylesheet';
        link.type = 'text/css';
        link.href = 'https://trianglecms.com/react/template-default.css';
        // link.href = 'https://raineconor.github.io/triangle/build/template-default.css';
        // link.href += "?a=" + Math.random(); // for testing only. circumvents caching.
        template.contentDocument.head.appendChild(link);

        let style = document.createElement("style");
        style.id = "styles";
        template.contentDocument.head.appendChild(style);

        let mainsheet = template.contentDocument.getElementById("styles").sheet;
        mainsheet.insertRule(breakpointMap.xs.mediaRule + "{}");
        mainsheet.insertRule(breakpointMap.sm.mediaRule + "{}");
        mainsheet.insertRule(breakpointMap.md.mediaRule + "{}");
        mainsheet.insertRule(breakpointMap.lg.mediaRule + "{}");
        mainsheet.insertRule(breakpointMap.xl.mediaRule + "{}");
        mainsheet.insertRule(breakpointMap.xxl.mediaRule + "{}");

        setIframeLoaded(true);
    }

    let iframeDependent = <></>;
    if (iframeLoaded) {
        iframeDependent = <>
            <OptionsBar />
            <KeyBinds />
            <CodeDraggable />
            <MirrorMode />
        </>
    }

    const breakpoint = useSelector((state) => state.triangle.breakpoint);
    const breakpointWidth = breakpointMap[breakpoint].width;
    return <>
        {iframeDependent}
        <div id="marginFix" />
        <div id="iframeWrapper">
            <SelectionBorder />
            <iframe id="template" title="template"
                onLoad={onIframeLoad}
                style={{ width: breakpointWidth }} />
        </div>
    </>
}

export default Triangle;