import React, {useState} from 'react';
import { useDispatch } from 'react-redux';
import { selectItem, toggleMirrorMode } from '../../store/triangleSlice'
import ListItemText from '@mui/material/ListItemText';
import ListItemIcon from '@mui/material/ListItemIcon';
import ButtonUnstyled from '@mui/base/ButtonUnstyled';
import Switch from '@mui/material/Switch';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import PublishIcon from '@mui/icons-material/Publish';
import VisibilityIcon from '@mui/icons-material/Visibility';
import WebAssetIcon from '@mui/icons-material/WebAsset';
import FolderZipOutlinedIcon from '@mui/icons-material/FolderZipOutlined';
import CloudUploadOutlinedIcon from '@mui/icons-material/CloudUploadOutlined';
import CodeIcon from '@mui/icons-material/Code';

import {updateMirror} from '../MirrorMode';

import '../OptionsBar.css';

export default function OptionsExport() {
    const [mirrorSwitch, setMirrorSwitch] = useState(false);
    const dispatch = useDispatch();

    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleSwitch = () => {
        dispatch(toggleMirrorMode(!mirrorSwitch));
        setMirrorSwitch(!mirrorSwitch);
    };

    const handleClose = (option) => {
        dispatch(selectItem(false));
        setAnchorEl(null);

        switch(option) {
            case "preview" :
                updateMirror();
                break;
            default: break;
        }
    };

    return (
        <>
            <ButtonUnstyled className="OptionsBtn"
                aria-controls={open ? 'basic-menu' : undefined}
                aria-haspopup="true"
                aria-expanded={open ? 'true' : undefined}
                onClick={handleClick}>
                <PublishIcon sx={{ fontSize: 20 }} />
            </ButtonUnstyled>
            <Menu
                anchorEl={anchorEl}
                open={open}
                onClose={() => { handleClose() }}
                MenuListProps={{
                    'aria-labelledby': 'basic-button',
                }}>

                <MenuItem onClick={() => { handleClose("preview") }}>
                    <ListItemIcon>
                        <VisibilityIcon fontSize={"small"} />
                    </ListItemIcon>
                    <ListItemText sx={{marginRight:2}}>Preview</ListItemText>
                </MenuItem>

                <MenuItem disableRipple>
                    <ListItemIcon>
                        <WebAssetIcon fontSize={"small"} />
                    </ListItemIcon>
                    <ListItemText sx={{marginRight:2}}>Mirror Mode</ListItemText>
                    <Switch size={"small"} checked={mirrorSwitch} onClick={handleSwitch} />
                </MenuItem>

                <MenuItem onClick={() => { handleClose() }}>
                    <ListItemIcon>
                        <CloudUploadOutlinedIcon fontSize={"small"} />
                    </ListItemIcon>
                    <ListItemText sx={{marginRight:2}}>Publish</ListItemText>
                </MenuItem>

                <MenuItem onClick={() => { handleClose() }}>
                    <ListItemIcon>
                        <FolderZipOutlinedIcon fontSize={"small"} />
                    </ListItemIcon>
                    <ListItemText sx={{marginRight:2}}>Zip</ListItemText>
                </MenuItem>

                <MenuItem onClick={() => { handleClose() }}>
                    <ListItemIcon>
                        <CodeIcon fontSize={"small"} />
                    </ListItemIcon>
                    <ListItemText sx={{marginRight:2}}>Raw code</ListItemText>
                </MenuItem>
            </Menu>
        </>
    );
}
